export default {
	data() {
		return {
			name : "",
			tel : "",
			address : "选择地址",
			moreaddress : "",
			userId:this.user.uid,
			text:1,
			title:"新增地址",
			addressId:""
		}
	},
	onLoad(options) {
		if(options.obj!=null){
			let arr=JSON.parse(options.obj)
			this.name = arr.userName;
			this.tel = arr.userTel;
			this.address = arr.addressTwo;
			this.moreaddress = arr.addressOne;
			this.text=2,
			this.title="修改地址",
			this.addressId=arr.addressId;
			console.log(arr)
		}
	},
	
	onShow(options){
		
	},
	methods: {
		 back : function(){
		    uni.navigateBack({
		 
		    });
		 },
		 
	    openmap : function(){
	    	var that = this;
	    	uni.chooseLocation({
	    	    success: function (res) {
					that.address = res.address;
	    	    }
	    	});
	    },
		
		  // 确定点击地址
		addbtn : function(){
			if(this.name == "" || this.tel == "" || this.address == "选择地址" || this.moreaddress == ""){
				uni.showToast({
					title : "请完整的填写信息",
					icon : "none"
				})
			}else{
				console.log(this.text==1)
				if(this.text==1){
					console.log(11)
					let that=this;
					let url = that.url1+"/dinghai/Address/insertAddress";
					uni.showLoading({
						title:"正在添加",mask: true
					})
					uni.request({
						 url : url,
								 header : {
									 "Content-type" : "application/x-www-form-urlencoded"
								 },
								 method : 'post',
								 data : {
									  userName:that.name,
									  userTel:that.tel,
									  userId:that.userId,
									  addressOne:that.address,
									  addressTwo:that.moreaddress
								 },
								 success : function(res){
									 console.log(res)
								     if(res.statusCode===200){
										 console.log(res)
										 uni.navigateBack({
										 	
										 });
									 }else{
										 uni.showToast({
										 	title:"您的网络开了小差",
											icon:"none"
										 })
									 }
								 },
								 fail : function(err){
									 console.log(err);
									 uni.showToast({
									 	title:"连接服务器失败",
										icon:"none"
									 })
								 }
					});
				}else if(this.text==2){
					let that=this;
					let url = that.url1+"/dinghai/Address/UpdateAddress?addressId="+that.addressId;
					uni.showLoading({
						title:"正在添加",mask: true
					})
					uni.request({
						 url : url,
								 header : {
									 "Content-type" : "application/x-www-form-urlencoded"
								 },
								 method : 'post',
								 data : {
									  userName:that.name,
									  userTel:that.tel,
									  userId:that.userId,
									  addressOne:that.address,
									  addressTwo:that.moreaddress,
									  addressId:that.addressId
								 },
								 success : function(res){
								     if(res.statusCode===200){
										 console.log(res)
										 uni.navigateBack({
										 	
										 });
									 }else{
										 uni.showToast({
										 	title:"您的网络开了小差",
											icon:"none"
										 })
									 }
								 },
								 fail : function(err){
									 console.log(err);
									 uni.showToast({
									 	title:"连接服务器失败",
										icon:"none"
									 })
								 }
					});
				}
			}
		}
	}
}
